import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import moment, { locale } from 'moment';
import axios from 'axios';
import 'moment/locale/pt-br';
import Toast from './Toast';
import salvar from '../images/salvar.svg';
import deletar from '../images/deletar.svg';
import alerta from '../images/alerta.svg';
import lock from '../images/lock.svg';

function Pagamento() {
  moment.locale('pt-br');
  var html = 'https://scope-server.herokuapp.com';

  const changeState = useDispatch();
  const viewpagamento = useSelector((state) => state.pagamento);
  // recuperando usuário.
  const idusuario = useSelector((state) => state.idusuario);
  const idhospital = useSelector((state) => state.idhospital);
  const idunidade = useSelector((state) => state.idunidade);
  // recuperando informações.
  const idescala = useSelector((state) => state.idescala);
  const idplantonista = useSelector((state) => state.idplantonista);
  const entrada = useSelector((state) => state.entradaplantao);
  const saida = useSelector((state) => state.saidaplantao);

  useEffect(() => {
    if (viewpagamento === 1) {
      loadUsuarios();
      loadPagamentos();
    } else {
    }
    // eslint-disable-next-line
  }, [viewpagamento]);

  // carregar usuário.
  const [usuarios, setusuarios] = useState([]);
  const [nomeplantonista, setnomeplantonista] = useState('');
  const [funcaoplantonista, setfuncaoplantonista] = useState('');
  const [tipoplantonista, settipoplantonista] = useState(0);
  const [conselhoplantonista, setconselhoplantonista] = useState(0);
  const loadUsuarios = () => {
    axios.get(html + '/usuarios').then((response) => {
      var x = response.data;
      setusuarios(response.data);
      // recuperando informações do plantonista escalado.
      setnomeplantonista(x.filter(item => item.id == idplantonista).map(item => item.nome));
      setfuncaoplantonista(x.filter(item => item.id == idplantonista).map(item => item.funcao));
      settipoplantonista(x.filter(item => item.id == idplantonista).map(item => item.tipo));
      setconselhoplantonista(x.filter(item => item.id == idplantonista).map(item => item.conselho));
    });
  }

  // carregar registros de pagamento.
  const [pagamentos, setpagamentos] = useState([]);
  const [idpagamento, setidpagamento] = useState([]);
  const loadPagamentos = () => {
    axios.get(html + '/pagamentos').then((response) => {
      setpagamentos(response.data);
      setidpagamento(pagamentos.filter(item => item.idcalendario == idescala).map(item => item.id));
    });
  }

  // inserir registro de pagamento.
  const insertPagamento = () => {
    var obj = {

    }
    axios.post(html + '/insertpagamento', obj).then(() => {
      toast(1, '#52be80', 'REGISTRO DE PAGAMENTO INSERIDO COM SUCESSO.', 3000);
    })
  }

  // atualizar um registro de pagamento.
  const updatePagamento = () => {
    var obj = {

    }
    axios.post(html + '/updatepagamento/' + idpagamento, obj).then(() => {
      toast(1, '#52be80', 'REGISTRO DE PAGAMENTO ALTERADO COM SUCESSO.', 3000);
    });
  }

  // deletar um registro de pagamento.
  const deletePagamento = () => {
    axios.get(html + '/deletepagamento/' + idpagamento).then(() => {
      toast(1, '#52be80', 'REGISTRO DE PAGAMENTO EXCLUÍDO COM SUCESSO.', 3000);
    });
  }

  // TOASTS.
  // função para construção dos toasts.
  const [valor, setvalor] = useState(0);
  const [cor, setcor] = useState('transparent');
  const [mensagem, setmensagem] = useState('');
  const [tempo, settempo] = useState(2000);
  const toast = (value, color, message, time) => {
    setvalor(value);
    setcor(color);
    setmensagem(message);
    settempo(time);
    setTimeout(() => {
      setvalor(0);
    }, time + 1000);
  }

  const sair = () => {
    changeState({
      type: 'CALENDARIO_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadIdhospital: idhospital,
      payloadIdunidade: idunidade,
    });
  }

  // renderização do componente.
  if (viewpagamento === 1) {
    return (
      <div>
        <div
          className="popup-bottom"
        >
          <div className="popup-screen" style={{ position: 'relative' }}>
            <div className="title" style={{ fontSize: 16 }}>PAGAMENTO DE PLANTÃO</div>
            <button
              className="red-button"
              onClick={() => sair()}
              title="SAIR."
              style={{
                position: 'absolute',
                width: 50,
                top: 5,
                left: 5,
              }}
            >
              <img
                alt=""
                src={deletar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
            <div className="default">
              <div className="title">{nomeplantonista + ' - ' + conselhoplantonista}</div>
              <div
                className="title"
                style={{ fontSize: 16, color: '#52be80', fontWeight: 'bolder' }}>
                {JSON.stringify(entrada).substring(3, 17) + ' - ' + JSON.stringify(saida).substring(3, 17)}
              </div>
              <div className="default" style={{ flexDirection: 'row' }}>
                <div id="dados do pagamento" className="default">
                  <div className="default" style={{ flexDirection: 'row', margin: 0, padding: 0 }}>
                    <button
                      className="green-button"
                      // onClick={() => selectValor()}
                      title="VALOR DO PLANTÃO."
                      style={{
                        width: 150,
                      }}
                    >
                      {'R$ 1800,00'}
                    </button>
                    <button
                      className="green-button"
                      // onClick={() => selectAcrescimo()}
                      title="ACRÉSCIMO (%)"
                      style={{
                        width: 50,
                      }}
                    >
                      {'15%'}
                    </button>
                    <button
                      className="red-button"
                      // onClick={() => selectDeducao()}
                      title="DEDUÇÃO (%)."
                      style={{
                        width: 50,
                      }}
                    >
                      {'2%'}
                    </button>
                    <button
                      className="blue-button"
                      // onClick={() => selectDeducao()}
                      title="DEDUÇÃO (%)."
                      style={{
                        width: 150,
                      }}
                    >
                      {'TOTAL'}
                    </button>
                    <button
                      className="green-button"
                      // onClick={() => insertPagamento()} // alternar com atualizar pagamento.
                      title="REGISTRAR PAGAMENTO."
                      style={{
                        width: 50,
                      }}
                    >
                      <img
                        alt=""
                        src={salvar}
                        style={{
                          margin: 10,
                          height: 30,
                          width: 30,
                        }}
                      ></img>
                    </button>
                  </div>
                  <div className="default" style={{ flexDirection: 'row', width: '100%', margin: 0 }}>
                    <textarea
                      autoComplete="off"
                      className="textarea"
                      placeholder="OBSERVAÇÕES SOBRE O ACRÉSCIMO."
                      onFocus={(e) => (e.target.placeholder = '')}
                      onBlur={(e) => (e.target.placeholder = 'OBSERVAÇÕES SOBRE O ACRÉSCIMO.')}
                      title="OBSERVAÇÕES SOBRE O ACRÉSCIMO."
                      style={{
                        width: '100%',
                        height: 50,
                        marginLeft: 0,
                      }}
                      type="text"
                      maxLength={200}
                      id="inputAcrescimo"
                    ></textarea>
                    <textarea
                      autoComplete="off"
                      className="textarea"
                      placeholder="OBSERVAÇÕES SOBRE A DEDUÇÃO."
                      onFocus={(e) => (e.target.placeholder = '')}
                      onBlur={(e) => (e.target.placeholder = 'OBSERVAÇÕES SOBRE A DEDUÇÃO.')}
                      title="OBSERVAÇÕES SOBRE A DEDUÇÃO."
                      style={{
                        width: '100%',
                        height: 50,
                        marginLeft: 0,
                        marginRight: 0,
                      }}
                      type="text"
                      maxLength={200}
                      id="inputDeducao"
                    ></textarea>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <Toast valor={valor} cor={cor} mensagem={mensagem} tempo={tempo}></Toast>
        </div>
      </div >
    )
  } else {
    return null;
  }
}

export default Pagamento;